<template>
  <div data-cy="trip-details-card" class="max-w-md mx-auto bg-white rounded-xl shadow-md overflow-hidden md:max-w-2xl">
    <div class="md:flex">
      <div class="md:flex-shrink-0">
        <img class="h-48 w-full object-cover md:h-full md:w-48" :src="image" alt="Trip Image">
      </div>
      <div class="p-8 flex flex-col justify-between">
        <div>
          <div class="text-lg font-semibold">{{ name }}</div>
          <div class="text-sm text-indigo-500">{{ destination }}</div>
          <div class="mt-2 text-gray-500">{{ description }}</div>
          <dl class="grid grid-cols-2 gap-x-4 gap-y-2 mt-4">
            <template v-if="reviewRating">
              <dt class="font-bold">Reviews</dt>
              <dd>
                <Rating :rating="reviewRating" class="rating--small" />
              </dd>
            </template>
            <template v-if="duration">
              <dt class="font-bold">Trip duration</dt>
              <dd>{{ duration }} {{ duration > 1 ? "days" : "day" }}</dd>
            </template>
            <template v-if="startCity">
              <dt class="font-bold">Trip start</dt>
              <dd>{{ startCity }}</dd>
            </template>
            <template v-if="endCity">
              <dt class="font-bold">Trip end</dt>
              <dd>{{ endCity }}</dd>
            </template>
            <template v-if="styles">
              <dt class="font-bold">Trip style</dt>
              <dd>{{ formattedStyles }}</dd>
            </template>
            <template v-if="physicalRating">
              <dt class="font-bold">Physical rating</dt>
              <dd>
                <Rating :rating="physicalRating" type="rectangle" />
                <Popper :show-popper="showPhysicalRatingInfo" @hide="togglePhysicalRatingInfo(false)">
                  <template #reference>
                    <span @click.prevent="togglePhysicalRatingInfo(true)">
                      <Icon class="cursor-pointer" name="info-circle-outline" />
                    </span>
                  </template>
                  <template #content>
                    Indicates how physically exerting a trip is, and how fit you
                    will need to be to enjoy it.
                    <AppLink to="/about/physical-rating">More info on physical ratings</AppLink>
                  </template>
                </Popper>
              </dd>
            </template>
            <template v-if="code">
              <dt class="font-bold">Trip code</dt>
              <dd>{{ code }}</dd>
            </template>
          </dl>
        </div>
        <div class="mt-4 text-right text-lg font-semibold">
          <span class="text-gray-700">From</span> <span class="text-blue-500">{{ price }}</span>
        </div>
        <div v-if="footerButton" class="mt-4 flex justify-end">
          <button class="bg-blue-500 text-white rounded px-4 py-2 hover:bg-blue-700">{{ footerButtonText }}</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Rating from '../components/Rating.vue';
import Popper from '../components/Popper.vue';
import AppLink from '../components/AppLink.vue';
import Icon from '../components/Icon.vue';

export default {
  name: "TripDetailsCard",
  components: {
    Rating,
    Popper,
    AppLink,
    Icon
  },
  props: {
    image: {
      type: String,
      required: true
    },
    destination: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    description: {
      type: String,
      required: true
    },
    reviewRating: {
      type: Number,
      required: false,
      default: null
    },
    duration: {
      type: Number,
      required: false,
      default: null
    },
    startCity: {
      type: String,
      required: false,
      default: null
    },
    endCity: {
      type: String,
      required: false,
      default: null
    },
    styles: {
      type: Array,
      required: false,
      default: () => []
    },
    physicalRating: {
      type: Number,
      required: false,
      default: null
    },
    code: {
      type: String,
      required: false,
      default: null
    },
    price: {
      type: String,
      required: true
    },
    footerButton: {
      type: Boolean,
      default: false
    },
    footerButtonText: {
      type: String,
      default: 'Book Now'
    }
  },
  data() {
    return {
      showPhysicalRatingInfo: false
    };
  },
  computed: {
    formattedStyles() {
      return this.styles.join(", ");
    }
  },
  methods: {
    togglePhysicalRatingInfo(show) {
      this.showPhysicalRatingInfo = show;
    }
  }
};
</script>

